.header__block {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 80px;
}

.header__title {
	font-size: 24px;
	color: #fff;
	margin: 0;
}

.header__links {
	display: flex;
	margin: 0;
	align-items: center;
	color: #fff;
	list-style-type: none;
}

.header__links li {
	margin-right: 20px;
	font-size: 18px;
}
.error {
	color: #fff;
	background: red;
	border-radius: 8px;
	border: 1px solid #fff;
	padding: 15px 0;
	margin: 20px;
	font-size: 20px;
	font-weight: 600;
	display: flex;
	justify-content: center;
	width: 200px;
}
.random-block {
    background-color: #fff;
    padding: 25px 25px 15px 25px;
    margin-bottom: 40px;
    text-align: center;
}

.random-block h4 {
    margin-bottom: 20px;
    text-align: center;
}

.term {
    font-weight: bold;
}
