.ldio-h1818r0r1hh div {
	position: absolute;
	border-width: 4px;
	border-style: solid;
	opacity: 1;
	border-radius: 50%;
	-webkit-animation: ldio-h1818r0r1hh 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
	        animation: ldio-h1818r0r1hh 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.ldio-h1818r0r1hh div:nth-child(1) {
	border-color: #e90c59;
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}

.ldio-h1818r0r1hh div:nth-child(2) {
	border-color: #46dff0;
	-webkit-animation-delay: -0.5s;
	        animation-delay: -0.5s;
}

